define('core/modules/tooltip',["jquery"], function($) {
    return {
        init: function () {
            console.log("tooltip");
            $(document).click(function (event) {
                if ($(event.target).closest('.js-tooltip-toggle').length) {
                    if ($('.js-tooltip').css("display") == 'none') {
                        $('.js-tooltip').css("display", "flex");
                    } else {
                        $('.js-tooltip').css("display", "none");
                    }
                } else {
                    $('.js-tooltip').css("display", "none");
                }
            })
        }
    };
});

